import { lighten, makeStyles } from "@material-ui/core/styles";
import React, { useCallback, useState } from "react";
import Stepper from "@material-ui/core/Stepper";
import StepLabel from "@material-ui/core/StepLabel";
import Step from "@material-ui/core/Step";
import Typography from "@material-ui/core/Typography";
import { useDispatch } from "react-redux";
import { toggleSnackbar } from "../../../../actions";
import Link from "@material-ui/core/Link";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Input from "@material-ui/core/Input";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Radio from "@material-ui/core/Radio";
import Collapse from "@material-ui/core/Collapse";
import Button from "@material-ui/core/Button";
import API from "../../../../middleware/Api";
import MagicVar from "../../Dialogs/MagicVar";
import DomainInput from "../../Common/DomainInput";
import SizeInput from "../../Common/SizeInput";
import {useHistory} from "react-router";
import Alert from "@material-ui/lab/Alert";
import {randomStr} from "../../../../utils";

const useStyles = makeStyles(theme => ({
    stepContent: {
        padding: "16px 32px 16px 32px"
    },
    form: {
        maxWidth: 400,
        marginTop: 20
    },
    formContainer: {
        [theme.breakpoints.up("md")]: {
            padding: "0px 24px 0 24px"
        }
    },
    subStepContainer: {
        display: "flex",
        marginBottom: 20,
        padding:10,
        transition: theme.transitions.create("background-color", {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen
        }),
        "&:focus-within":{
            backgroundColor:theme.palette.background.default,
        }
    },
    stepNumber: {
        width: 20,
        height: 20,
        backgroundColor: lighten(theme.palette.secondary.light, 0.2),
        color: theme.palette.secondary.contrastText,
        textAlign: "center",
        borderRadius: " 50%"
    },
    stepNumberContainer: {
        marginRight: 10,
    },
    stepFooter:{
        marginTop: 32,
    },
    button: {
        marginRight: theme.spacing(1),
    },
    "@global":{
        "code":{
            color: "rgba(0, 0, 0, 0.87)",
            display: "inline-block",
            padding: "2px 6px",
            fontSize: "14px",
            fontFamily:" Consolas, \"Liberation Mono\", Menlo, Courier, monospace",
            borderRadius: "2px",
            backgroundColor: "rgba(255,229,100,0.1)",
        },
        "pre":{
            margin: "24px 0",
            padding: "12px 18px",
            overflow: "auto",
            direction: "ltr",
            borderRadius: "4px",
            backgroundColor: "#272c34",
            color:"#fff",
        }
    },

}));

const steps = [
    {
        title: "",
        optional: false
    },
    {
        title: "",
        optional: false
    },
    {
        title: "",
        optional: false
    },
    {
        title: "",
        optional: false
    },
    {
        title: "",
        optional: false
    }
];

export default function RemoteGuide(props) {
    const classes = useStyles();
    const history = useHistory();

    const [activeStep, setActiveStep] = useState(0);
    const [loading, setLoading] = useState(false);
    const [skipped, ] = React.useState(new Set());
    const [magicVar,setMagicVar] = useState("");
    const [useCDN,setUseCDN] = useState("false");
    const [policy, setPolicy] = useState(props.policy?props.policy:{
        Type:"remote",
        Name:"",
        Server:"https://example.com:5212",
        SecretKey:randomStr(64),
        DirNameRule: "uploads/{year}/{month}/{day}",
        AutoRename: "true",
        FileNameRule: "{randomkey8}_{originname}",
        IsOriginLinkEnable:"false",
        BaseURL:"",
        IsPrivate:"true",
        MaxSize:"0",
        OptionsSerialized:{
            file_type:"",
        },
    });

    const handleChange = name => event => {
        setPolicy({
            ...policy,
            [name]: event.target.value
        });
    };

    const handleOptionChange = name => event => {
        setPolicy({
            ...policy,
            OptionsSerialized:{
                ...policy.OptionsSerialized,
                [name]: event.target.value
            },
        });
    };

    const isStepSkipped = step => {
        return skipped.has(step);
    };

    const dispatch = useDispatch();
    const ToggleSnackbar = useCallback(
        (vertical, horizontal, msg, color) =>
            dispatch(toggleSnackbar(vertical, horizontal, msg, color)),
        [dispatch]
    );

    const testSlave = ()=>{
        setLoading(true);

        // 
        API.post("/admin/policy/test/slave", {
            server: policy.Server,
            secret:policy.SecretKey,
        })
            .then(() => {
                ToggleSnackbar("top", "right", "", "success");
            })
            .catch(error => {
                ToggleSnackbar("top", "right", error.message, "error");
            })
            .then(() => {
                setLoading(false);
            });
    };

    const submitPolicy = e => {
        e.preventDefault();
        setLoading(true);

        const policyCopy = {...policy};
        policyCopy.OptionsSerialized = {...policyCopy.OptionsSerialized};

        // 
        if (useCDN === "false" || policy.IsOriginLinkEnable === "false"){
            policyCopy.BaseURL = ""
        }

        // 
        policyCopy.AutoRename = policyCopy.AutoRename === "true";
        policyCopy.IsOriginLinkEnable = policyCopy.IsOriginLinkEnable === "true";
        policyCopy.MaxSize = parseInt(policyCopy.MaxSize);
        policyCopy.IsPrivate = policyCopy.IsPrivate === "true";
        policyCopy.OptionsSerialized.file_type = policyCopy.OptionsSerialized.file_type.split(",");
        if (policyCopy.OptionsSerialized.file_type.length === 1 && policyCopy.OptionsSerialized.file_type[0] === ""){
            policyCopy.OptionsSerialized.file_type = [];
        }

        API.post("/admin/policy", {
            policy: policyCopy
        })
            .then(() => {
                ToggleSnackbar("top", "right", "" + (props.policy ? "" : ""), "success");
                setActiveStep(5);
            })
            .catch(error => {
                ToggleSnackbar("top", "right", error.message, "error");
            })
            .then(() => {
                setLoading(false);
            });

        setLoading(false);

    }

    return (
        <div>
            <Typography variant={"h6"}>{props.policy ? "" : ""}</Typography>
            <Stepper activeStep={activeStep}>
                {steps.map((label, index) => {
                    const stepProps = {};
                    const labelProps = {};
                    if (label.optional) {
                        labelProps.optional = (
                            <Typography variant="caption"></Typography>
                        );
                    }
                    if (isStepSkipped(index)) {
                        stepProps.completed = false;
                    }
                    return (
                        <Step key={label.title} {...stepProps}>
                            <StepLabel {...labelProps}>{label.title}</StepLabel>
                        </Step>
                    );
                })}
            </Stepper>

            {activeStep === 0 && (
                <form
                    className={classes.stepContent}
                    onSubmit={e=>{
                        e.preventDefault();
                        setActiveStep(1);
                    }}
                >
                    <Alert severity="info" style={{marginBottom:10,}}>
                         Cloudreve 
                         HTTP 
                    </Alert>

                    <div className={classes.subStepContainer}>
                        <div className={classes.stepNumberContainer}>
                            <div className={classes.stepNumber}>1</div>
                        </div>
                        <div className={classes.subStepContent}>
                            <Typography variant={"body2"}>
                                 Cloudreve 
                            </Typography>
                        </div>
                    </div>

                    <div className={classes.subStepContainer}>
                        <div className={classes.stepNumberContainer}>
                            <div className={classes.stepNumber}>2</div>
                        </div>
                        <div className={classes.subStepContent}>
                            <Typography variant={"body2"}>
                                
                                
                            </Typography>
                            <div className={classes.form}>
                                <FormControl fullWidth>
                                    <InputLabel htmlFor="component-helper">
                                        
                                    </InputLabel>
                                    <Input
                                        required
                                        inputProps={{
                                            minlength:64,
                                        }}
                                        value={policy.SecretKey}
                                        onChange={handleChange("SecretKey")}
                                    />
                                </FormControl>
                            </div>
                        </div>
                    </div>

                    <div className={classes.subStepContainer}>
                        <div className={classes.stepNumberContainer}>
                            <div className={classes.stepNumber}>3</div>
                        </div>
                        <div className={classes.subStepContent}>
                            <Typography variant={"body2"}>
                                <br/> Cloudreve 
                                <code>conf.ini</code>/ Cloudreve
                                
                            </Typography>
                                <pre>
                                    [System]<br/>
                                    Mode = slave<br/>
                                    Listen = :5212<br/>
                                    <br/>
                                    [Slave]<br/>
                                    Secret = {policy.SecretKey}<br/>
                                    <br/>
                                    [CORS]<br/>
                                    AllowOrigins = *<br/>
                                    AllowMethods = OPTIONS,GET,POST<br/>
                                    AllowHeaders = *<br/>
                                </pre>
                            <Typography variant={"body2"}>
                                
                                <ul>
                                    <li>
                                        <code>System</code>
                                        
                                        <code>mode</code>
                                        <code>slave</code>
                                    </li>
                                    <li>
                                        <code>Slave</code><code>Secret</code>
                                        
                                    </li>
                                    <li><code>CORS</code>
                                     Web 
                                    </li>
                                </ul>
                            </Typography>
                        </div>
                    </div>

                    <div className={classes.subStepContainer}>
                        <div className={classes.stepNumberContainer}>
                            <div className={classes.stepNumber}>4</div>
                        </div>
                        <div className={classes.subStepContent}>
                            <Typography variant={"body2"}>
                                <br/>
                                 HTTPS HTTPS 
                            </Typography>
                            <div className={classes.form}>
                                <FormControl fullWidth>
                                    <InputLabel htmlFor="component-helper">
                                        
                                    </InputLabel>
                                    <Input
                                        fullWidth
                                        required
                                        type={"url"}
                                        value={policy.Server}
                                        onChange={handleChange("Server")}
                                    />
                                </FormControl>
                            </div>
                        </div>
                    </div>

                    <div className={classes.subStepContainer}>
                        <div className={classes.stepNumberContainer}>
                            <div className={classes.stepNumber}>5</div>
                        </div>
                        <div className={classes.subStepContent}>
                            <Typography variant={"body2"}>
                                
                            </Typography>
                            <div className={classes.form}>
                                <Button
                                    disabled={loading}
                                    onClick={()=>testSlave()}
                                    variant={"outlined"}
                                    color={"primary"}
                                >
                                    
                                </Button>
                            </div>
                        </div>
                    </div>

                    <div className={classes.stepFooter}>
                        <Button
                            disabled={loading}
                            type={"submit"}
                            variant={"contained"}
                            color={"primary"}
                        >
                            
                        </Button>
                    </div>

                </form>
            )}

            {activeStep === 1 && (
                <form
                    className={classes.stepContent}
                    onSubmit={e=> {
                        e.preventDefault();
                        setActiveStep(2);
                    }}
                >
                    <div className={classes.subStepContainer}>
                        <div className={classes.stepNumberContainer}>
                            <div className={classes.stepNumber}>1</div>
                        </div>
                        <div className={classes.subStepContent}>
                            <Typography variant={"body2"}>
                                
                                 Cloudreve
                                {" "}
                                <Link
                                    color={"secondary"}
                                    href={"javascript:void()"}
                                    onClick={() => setMagicVar("path")}
                                >
                                    
                                </Link>{" "}
                                
                            </Typography>
                            <div className={classes.form}>
                                <FormControl fullWidth>
                                    <InputLabel htmlFor="component-helper">
                                        
                                    </InputLabel>
                                    <Input
                                        required
                                        value={policy.DirNameRule}
                                        onChange={handleChange("DirNameRule")}
                                    />
                                </FormControl>
                            </div>
                        </div>
                    </div>

                    <div className={classes.subStepContainer}>
                        <div className={classes.stepNumberContainer}>
                            <div className={classes.stepNumber}>2</div>
                        </div>
                        <div className={classes.subStepContent}>
                            <Typography variant={"body2"}>
                                
                                
                                {" "}
                                <Link
                                    color={"secondary"}
                                    href={"javascript:void()"}
                                    onClick={() => setMagicVar("file")}
                                >
                                    
                                </Link>{" "}
                                
                            </Typography>
                            <div className={classes.form}>
                                <FormControl required component="fieldset">
                                    <RadioGroup
                                        aria-label="gender"
                                        name="gender1"
                                        value={policy.AutoRename}
                                        onChange={handleChange("AutoRename")}
                                        row
                                    >
                                        <FormControlLabel
                                            value={"true"}
                                            control={
                                                <Radio color={"primary"} />
                                            }
                                            label=""
                                        />
                                        <FormControlLabel
                                            value={"false"}
                                            control={
                                                <Radio color={"primary"} />
                                            }
                                            label=""
                                        />
                                    </RadioGroup>
                                </FormControl>
                            </div>

                            <Collapse in={policy.AutoRename === "true"}>
                                <div className={classes.form}>
                                    <FormControl fullWidth>
                                        <InputLabel htmlFor="component-helper">
                                            
                                        </InputLabel>
                                        <Input
                                            required={policy.AutoRename === "true"}
                                            value={policy.FileNameRule}
                                            onChange={handleChange(
                                                "FileNameRule"
                                            )}
                                        />
                                    </FormControl>
                                </div>
                            </Collapse>
                        </div>
                    </div>

                    <div className={classes.stepFooter}>
                        <Button
                            color={"default"}
                            className={classes.button}
                            onClick={()=> setActiveStep(0)}
                        >
                            
                        </Button>
                        <Button
                            disabled={loading}
                            type={"submit"}
                            variant={"contained"}
                            color={"primary"}
                        >
                            
                        </Button>
                    </div>
                </form>
            )}

            {activeStep === 2 && (
                <form
                    className={classes.stepContent}
                    onSubmit={e=> {
                        e.preventDefault();
                       setActiveStep(3);
                    }}
                >
                    <div className={classes.subStepContainer}>
                        <div className={classes.stepNumberContainer}>
                            <div className={classes.stepNumber}>1</div>
                        </div>
                        <div className={classes.subStepContent}>
                            <Typography variant={"body2"}>
                                
                                <br />
                                
                            </Typography>

                            <div className={classes.form}>
                                <FormControl required component="fieldset">
                                    <RadioGroup
                                        required
                                        value={policy.IsOriginLinkEnable}
                                        onChange={handleChange(
                                            "IsOriginLinkEnable"
                                        )}
                                        row
                                    >
                                        <FormControlLabel
                                            value={"true"}
                                            control={
                                                <Radio color={"primary"} />
                                            }
                                            label=""
                                        />
                                        <FormControlLabel
                                            value={"false"}
                                            control={
                                                <Radio color={"primary"} />
                                            }
                                            label=""
                                        />
                                    </RadioGroup>
                                </FormControl>
                            </div>
                        </div>
                    </div>

                    <Collapse in={policy.IsOriginLinkEnable === "true"}>
                        <div className={classes.subStepContainer}>
                            <div className={classes.stepNumberContainer}>
                                <div className={classes.stepNumber}>2</div>
                            </div>
                            <div className={classes.subStepContent}>
                                <Typography variant={"body2"}>
                                    / CDN
                                    <br />
                                     URL  CDN 
                                </Typography>

                                <div className={classes.form}>
                                    <FormControl required component="fieldset">
                                        <RadioGroup
                                            required
                                            value={useCDN}
                                            onChange={e=>{
                                                if (e.target.value === "false"){
                                                    setPolicy({
                                                        ...policy,
                                                        BaseURL: ""
                                                    });
                                                }
                                                setUseCDN(e.target.value)
                                            }}
                                            row
                                        >
                                            <FormControlLabel
                                                value={"true"}
                                                control={
                                                    <Radio color={"primary"} />
                                                }
                                                label=""
                                            />
                                            <FormControlLabel
                                                value={"false"}
                                                control={
                                                    <Radio color={"primary"} />
                                                }
                                                label=""
                                            />
                                        </RadioGroup>
                                    </FormControl>
                                </div>

                            </div>
                        </div>

                        <Collapse in={useCDN === "true"}>
                            <div className={classes.subStepContainer}>
                                <div className={classes.stepNumberContainer}>
                                    <div className={classes.stepNumber}>3</div>
                                </div>
                                <div className={classes.subStepContent}>
                                    <Typography variant={"body2"}>
                                         CDN 
                                    </Typography>

                                    <div className={classes.form}>
                                        <DomainInput
                                            value={policy.BaseURL}
                                            onChange={handleChange("BaseURL")}
                                            required={policy.IsOriginLinkEnable === "true" && useCDN === "true"}
                                            label={"CDN "}
                                        />
                                    </div>

                                </div>
                            </div>
                        </Collapse>

                    </Collapse>

                    <div className={classes.stepFooter}>
                        <Button
                            color={"default"}
                            className={classes.button}
                            onClick={()=> setActiveStep(1)}
                        >
                            
                        </Button>
                        {" "}
                        <Button
                            disabled={loading}
                            type={"submit"}
                            variant={"contained"}
                            color={"primary"}
                        >
                            
                        </Button>
                    </div>

                </form>
            )}

            {activeStep === 3 && (
                <form className={classes.stepContent}
                      onSubmit={e=> {
                    e.preventDefault();
                    setActiveStep(4)
                }}
                >

                    <div className={classes.subStepContainer}>
                        <div className={classes.stepNumberContainer}>
                            <div className={classes.stepNumber}>1</div>
                        </div>
                        <div className={classes.subStepContent}>
                            <Typography variant={"body2"}>
                                
                            </Typography>

                            <div className={classes.form}>
                                <FormControl required component="fieldset">
                                    <RadioGroup
                                        required
                                        value={policy.MaxSize === "0" ? "false" : "true"}
                                        onChange={e=>{
                                            if(e.target.value === "true"){
                                                setPolicy({
                                                    ...policy,
                                                    MaxSize: "10485760",
                                                });
                                            }else{
                                                setPolicy({
                                                    ...policy,
                                                    MaxSize: "0",
                                                });
                                            }
                                        }}
                                        row
                                    >
                                        <FormControlLabel
                                            value={"true"}
                                            control={
                                                <Radio color={"primary"} />
                                            }
                                            label=""
                                        />
                                        <FormControlLabel
                                            value={"false"}
                                            control={
                                                <Radio color={"primary"} />
                                            }
                                            label=""
                                        />
                                    </RadioGroup>
                                </FormControl>
                            </div>
                        </div>
                    </div>

                    <Collapse in={policy.MaxSize !== "0"}>
                        <div className={classes.subStepContainer}>
                            <div className={classes.stepNumberContainer}>
                                <div className={classes.stepNumber}>2</div>
                            </div>
                            <div className={classes.subStepContent}>
                                <Typography variant={"body2"}>
                                    
                                </Typography>
                                <div className={classes.form}>
                                    <SizeInput
                                        value={policy.MaxSize}
                                        onChange={handleChange("MaxSize")}
                                        min={0}
                                        max={9223372036854775807}
                                        label={""}
                                    />
                                </div>

                            </div>
                        </div>

                    </Collapse>

                    <div className={classes.subStepContainer}>
                        <div className={classes.stepNumberContainer}>
                            <div className={classes.stepNumber}>{policy.MaxSize !== "0" ? "3" : "2"}</div>
                        </div>
                        <div className={classes.subStepContent}>
                            <Typography variant={"body2"}>
                                
                            </Typography>

                            <div className={classes.form}>
                                <FormControl required component="fieldset">
                                    <RadioGroup
                                        required
                                        value={policy.OptionsSerialized.file_type === "" ? "false" : "true"}
                                        onChange={e=>{
                                            if(e.target.value === "true"){
                                                setPolicy({
                                                    ...policy,
                                                    OptionsSerialized: {
                                                        ...policy.OptionsSerialized,
                                                        file_type:"jpg,png,mp4,zip,rar",
                                                    },
                                                });
                                            }else{
                                                setPolicy({
                                                    ...policy,
                                                    OptionsSerialized: {
                                                        ...policy.OptionsSerialized,
                                                        file_type:"",
                                                    },
                                                });
                                            }
                                        }}
                                        row
                                    >
                                        <FormControlLabel
                                            value={"true"}
                                            control={
                                                <Radio color={"primary"} />
                                            }
                                            label=""
                                        />
                                        <FormControlLabel
                                            value={"false"}
                                            control={
                                                <Radio color={"primary"} />
                                            }
                                            label=""
                                        />
                                    </RadioGroup>
                                </FormControl>
                            </div>
                        </div>
                    </div>

                    <Collapse in={policy.OptionsSerialized.file_type !== ""}>
                        <div className={classes.subStepContainer}>
                            <div className={classes.stepNumberContainer}>
                                <div className={classes.stepNumber}>{policy.MaxSize !== "0" ? "4" : "3"}</div>
                            </div>
                            <div className={classes.subStepContent}>
                                <Typography variant={"body2"}>
                                     , 
                                </Typography>
                                <div className={classes.form}>
                                    <FormControl fullWidth>
                                        <InputLabel htmlFor="component-helper">
                                            
                                        </InputLabel>
                                        <Input
                                            value={policy.OptionsSerialized.file_type}
                                            onChange={handleOptionChange(
                                                "file_type"
                                            )}
                                        />
                                    </FormControl>
                                </div>

                            </div>
                        </div>

                    </Collapse>

                    <div className={classes.stepFooter}>
                        <Button
                            color={"default"}
                            className={classes.button}
                            onClick={()=> setActiveStep(2)}
                        >
                            
                        </Button>
                        {" "}
                        <Button
                            disabled={loading}
                            type={"submit"}
                            variant={"contained"}
                            color={"primary"}
                        >
                            
                        </Button>
                    </div>

                </form>
            )}

            {activeStep === 4 && (
                <form className={classes.stepContent} onSubmit={submitPolicy}>
                    <div className={classes.subStepContainer}>
                        <div className={classes.stepNumberContainer}>

                        </div>
                        <div className={classes.subStepContent}>
                            <Typography variant={"body2"}>
                                
                            </Typography>
                            <div className={classes.form}>
                                <FormControl fullWidth>
                                    <InputLabel htmlFor="component-helper">
                                        
                                    </InputLabel>
                                    <Input
                                        required
                                        value={policy.Name}
                                        onChange={handleChange("Name")}
                                    />
                                </FormControl>
                            </div>
                        </div>
                    </div>
                    <div className={classes.stepFooter}>
                        <Button
                            color={"default"}
                            className={classes.button}
                            onClick={()=> setActiveStep(3)}
                        >
                            
                        </Button>
                        {" "}
                        <Button
                            disabled={loading}
                            type={"submit"}
                            variant={"contained"}
                            color={"primary"}
                        >
                            
                        </Button>
                    </div>

                </form>
            )}

            {activeStep === 5 && (
                <>
                <form className={classes.stepContent}>
                    <Typography>
                        {props.policy ? "" : ""}
                    </Typography>
                    <Typography variant={"body2"} color={"textSecondary"}>
                        
                    </Typography>
                </form>
                    <div className={classes.stepFooter}>
                        <Button
                            color={"primary"}
                            className={classes.button}
                            onClick={()=> history.push("/admin/policy")}
                        >
                            
                        </Button>
                    </div>
</>
             )}

            <MagicVar
                open={magicVar === "file"}
                isFile
                isSlave
                onClose={() => setMagicVar("")}
            />
            <MagicVar
                open={magicVar === "path"}
                isSlave
                onClose={() => setMagicVar("")}
            />
        </div>
    );
}
