import React, { useCallback, useEffect, useState } from "react";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import Input from "@material-ui/core/Input";
import FormHelperText from "@material-ui/core/FormHelperText";
import Button from "@material-ui/core/Button";
import API from "../../../middleware/Api";
import { useDispatch } from "react-redux";
import { toggleSnackbar } from "../../../actions";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import Chip from "@material-ui/core/Chip";
import SizeInput from "../Common/SizeInput";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Switch from "@material-ui/core/Switch";
import Collapse from "@material-ui/core/Collapse";
import {useHistory} from "react-router";

const useStyles = makeStyles(theme => ({
    root: {
        [theme.breakpoints.up("md")]: {
            marginLeft: 100
        },
        marginBottom: 40
    },
    form: {
        maxWidth: 400,
        marginTop: 20,
        marginBottom: 20
    },
    formContainer: {
        [theme.breakpoints.up("md")]: {
            padding: "0px 24px 0 24px"
        }
    }
}));

function getStyles(name, personName, theme) {
    return {
        fontWeight:
            personName.indexOf(name) === -1
                ? theme.typography.fontWeightRegular
                : theme.typography.fontWeightMedium
    };
}

export default function GroupForm(props) {
    const classes = useStyles();
    const [loading, setLoading] = useState(false);
    const [group, setGroup] = useState(
        props.group
            ? props.group
            : {
                  ID: 0,
                  Name: "",
                  MaxStorage: "1073741824", // 
                  ShareEnabled: "true", // 
                  WebDAVEnabled: "true", // 
                  SpeedLimit: "0", // 
                  PolicyList: ["1"], // ,
                  OptionsSerialized: {
                      // 
                      share_download: "true",
                      aria2_options: "{}", // json decode
                      compress_size: "0",
                      decompress_size: "0"
                  }
              }
    );
    const [policies, setPolicies] = useState({});

    const theme = useTheme();
    const history = useHistory();

    const dispatch = useDispatch();
    const ToggleSnackbar = useCallback(
        (vertical, horizontal, msg, color) =>
            dispatch(toggleSnackbar(vertical, horizontal, msg, color)),
        [dispatch]
    );

    useEffect(() => {
        API.post("/admin/policy/list", {
            page: 1,
            page_size: 10000,
            order_by: "id asc",
            conditions: {}
        })
            .then(response => {
                const res = {};
                response.data.items.forEach(v => {
                    res[v.ID] = v.Name;
                });
                setPolicies(res);
            })
            .catch(error => {
                ToggleSnackbar("top", "right", error.message, "error");
            });
    }, []);

    const handleChange = name => event => {
        setGroup({
            ...group,
            [name]: event.target.value
        });
    };

    const handleCheckChange = name => event => {
        const value = event.target.checked ? "true" : "false";
        setGroup({
            ...group,
            [name]: value
        });
    };

    const handleOptionCheckChange = name => event => {
        const value = event.target.checked ? "true" : "false";
        setGroup({
            ...group,
            OptionsSerialized: {
                ...group.OptionsSerialized,
                [name]: value
            }
        });
    };

    const handleOptionChange = name => event => {
        setGroup({
            ...group,
            OptionsSerialized: {
                ...group.OptionsSerialized,
                [name]: event.target.value
            }
        });
    };

    const submit = e => {
        e.preventDefault();
        const groupCopy = {
            ...group,
            OptionsSerialized: { ...group.OptionsSerialized }
        };

        // 
        ["ShareEnabled", "WebDAVEnabled"].forEach(v => {
            groupCopy[v] = groupCopy[v] === "true";
        });
        [
            "archive_download",
            "archive_task",
            "one_time_download",
            "share_download",
            "share_free",
            "aria2"
        ].forEach(v => {
            if (groupCopy.OptionsSerialized[v] !== undefined){
                groupCopy.OptionsSerialized[v] = groupCopy.OptionsSerialized[v] === "true";
            }
        });

        // 
        ["MaxStorage", "SpeedLimit"].forEach(v => {
            groupCopy[v] = parseInt(groupCopy[v]);
        });
        [
            "compress_size",
            "decompress_size",
        ].forEach(v => {
            if (groupCopy.OptionsSerialized[v] !== undefined){
                groupCopy.OptionsSerialized[v] = parseInt(groupCopy.OptionsSerialized[v]);
            }
        });
        groupCopy.PolicyList = groupCopy.PolicyList.map(v=>{
            return parseInt(v)
        });

        if (groupCopy.PolicyList.length < 1 && groupCopy.ID !== 3){
            ToggleSnackbar("top", "right", "", "warning");
            return;
        }

        // JSON
        try {
            groupCopy.OptionsSerialized.aria2_options = JSON.parse(groupCopy.OptionsSerialized.aria2_options);
        }catch (e) {
            ToggleSnackbar("top", "right", "Aria2 ", "warning");
            return;
        }

        setLoading(true);
        API.post("/admin/group", {
            group: groupCopy
        })
            .then(() => {
                history.push("/admin/group");
                ToggleSnackbar("top", "right", ""+ (props.group ? "" : ""), "success");
            })
            .catch(error => {
                ToggleSnackbar("top", "right", error.message, "error");
            })
            .then(() => {
                setLoading(false);
            });

    };

    return (
        <div>
            <form onSubmit={submit}>
                <div className={classes.root}>
                    <Typography variant="h6" gutterBottom>
                        {group.ID === 0 && ""}
                        {group.ID !== 0 && " " + group.Name}
                    </Typography>

                    <div className={classes.formContainer}>

                        {group.ID !== 3 && <>
                            <div className={classes.form}>
                                <FormControl fullWidth>
                                    <InputLabel htmlFor="component-helper">
                                        
                                    </InputLabel>
                                    <Input
                                        value={group.Name}
                                        onChange={handleChange("Name")}
                                        required
                                    />
                                    <FormHelperText id="component-helper-text">
                                        
                                    </FormHelperText>
                                </FormControl>
                            </div>

                            <div className={classes.form}>
                                <FormControl fullWidth>
                                    <InputLabel htmlFor="component-helper">
                                        
                                    </InputLabel>
                                    <Select
                                        labelId="demo-mutiple-chip-label"
                                        id="demo-mutiple-chip"
                                        multiple
                                        value={group.PolicyList}
                                        onChange={handleChange("PolicyList")}
                                        input={<Input id="select-multiple-chip" />}
                                        renderValue={selected => (
                                            <div className={classes.chips}>
                                                {selected.map(value => (
                                                    <Chip
                                                        style={{ margin: 2 }}
                                                        key={value}
                                                        size={"small"}
                                                        label={policies[value]}
                                                        className={classes.chip}
                                                    />
                                                ))}
                                            </div>
                                        )}
                                    >
                                        {Object.keys(policies).map(pid => (
                                            <MenuItem
                                                key={pid}
                                                value={pid}
                                                style={getStyles(
                                                    pid,
                                                    group.PolicyList,
                                                    theme
                                                )}
                                            >
                                                {policies[pid]}
                                            </MenuItem>
                                        ))}
                                    </Select>
                                    <FormHelperText id="component-helper-text">
                                        
                                    </FormHelperText>
                                </FormControl>
                            </div>

                            <div className={classes.form}>
                                <FormControl fullWidth>
                                    <SizeInput
                                        value={group.MaxStorage}
                                        onChange={handleChange("MaxStorage")}
                                        min={0}
                                        max={9223372036854775807}
                                        label={""}
                                        required
                                    />
                                </FormControl>
                                <FormHelperText id="component-helper-text">
                                    
                                </FormHelperText>
                            </div>
                        </>}


                        <div className={classes.form}>
                            <FormControl fullWidth>
                                <SizeInput
                                    value={group.SpeedLimit}
                                    onChange={handleChange("SpeedLimit")}
                                    min={0}
                                    max={9223372036854775807}
                                    label={""}
                                    suffix={"/s"}
                                    required
                                />
                            </FormControl>
                            <FormHelperText id="component-helper-text">
                                 0 
                                
                            </FormHelperText>
                        </div>

                        {group.ID !== 3 && <div className={classes.form}>
                            <FormControl fullWidth>
                                <FormControlLabel
                                    control={
                                        <Switch
                                            checked={
                                                group.ShareEnabled === "true"
                                            }
                                            onChange={handleCheckChange(
                                                "ShareEnabled"
                                            )}
                                        />
                                    }
                                    label=""
                                />
                                <FormHelperText id="component-helper-text">
                                    
                                </FormHelperText>
                            </FormControl>
                        </div>}


                        <div className={classes.form}>
                            <FormControl fullWidth>
                                <FormControlLabel
                                    control={
                                        <Switch
                                            checked={
                                                group.OptionsSerialized
                                                    .share_download === "true"
                                            }
                                            onChange={handleOptionCheckChange(
                                                "share_download"
                                            )}
                                        />
                                    }
                                    label=""
                                />
                                <FormHelperText id="component-helper-text">
                                    
                                </FormHelperText>
                            </FormControl>
                        </div>

                        <div className={classes.form}>
                            <FormControl fullWidth>
                                <FormControlLabel
                                    control={
                                        <Switch
                                            checked={
                                                group.OptionsSerialized
                                                    .share_free === "true"
                                            }
                                            onChange={handleOptionCheckChange(
                                                "share_free"
                                            )}
                                        />
                                    }
                                    label=""
                                />
                                <FormHelperText id="component-helper-text">
                                    
                                </FormHelperText>
                            </FormControl>
                        </div>

                        {group.ID !== 3 && <div className={classes.form}>
                            <FormControl fullWidth>
                                <FormControlLabel
                                    control={
                                        <Switch
                                            checked={
                                                group.WebDAVEnabled === "true"
                                            }
                                            onChange={handleCheckChange(
                                                "WebDAVEnabled"
                                            )}
                                        />
                                    }
                                    label="WebDAV"
                                />
                                <FormHelperText id="component-helper-text">
                                     WebDAV 
                                </FormHelperText>
                            </FormControl>
                        </div>}

                        <div className={classes.form}>
                            <FormControl fullWidth>
                                <FormControlLabel
                                    control={
                                        <Switch
                                            checked={
                                                group.OptionsSerialized
                                                    .one_time_download ===
                                                "true"
                                            }
                                            onChange={handleOptionCheckChange(
                                                "one_time_download"
                                            )}
                                        />
                                    }
                                    label=""
                                />
                                <FormHelperText id="component-helper-text">
                                    
                                </FormHelperText>
                            </FormControl>
                        </div>

                        {group.ID !== 3 && <div className={classes.form}>
                            <FormControl fullWidth>
                                <FormControlLabel
                                    control={
                                        <Switch
                                            checked={
                                                group.OptionsSerialized
                                                    .aria2 === "true"
                                            }
                                            onChange={handleOptionCheckChange(
                                                "aria2"
                                            )}
                                        />
                                    }
                                    label=""
                                />
                                <FormHelperText id="component-helper-text">
                                    
                                </FormHelperText>
                            </FormControl>
                        </div>}


                        <Collapse in={group.OptionsSerialized.aria2 === "true"}>
                            <div className={classes.form}>
                                <FormControl fullWidth>
                                    <InputLabel htmlFor="component-helper">
                                        Aria2 
                                    </InputLabel>
                                    <Input
                                        multiline
                                        value={
                                            group.OptionsSerialized
                                                .aria2_options
                                        }
                                        onChange={handleOptionChange(
                                            "aria2_options"
                                        )}
                                    />
                                    <FormHelperText id="component-helper-text">
                                        
                                        JSON
                                        
                                        Aria2 
                                    </FormHelperText>
                                </FormControl>
                            </div>
                        </Collapse>

                        <div className={classes.form}>
                            <FormControl fullWidth>
                                <FormControlLabel
                                    control={
                                        <Switch
                                            checked={
                                                group.OptionsSerialized
                                                    .archive_download === "true"
                                            }
                                            onChange={handleOptionCheckChange(
                                                "archive_download"
                                            )}
                                        />
                                    }
                                    label=""
                                />
                                <FormHelperText id="component-helper-text">
                                    
                                </FormHelperText>
                            </FormControl>
                        </div>

                        {group.ID !== 3 && <div className={classes.form}>
                            <FormControl fullWidth>
                                <FormControlLabel
                                    control={
                                        <Switch
                                            checked={
                                                group.OptionsSerialized
                                                    .archive_task === "true"
                                            }
                                            onChange={handleOptionCheckChange(
                                                "archive_task"
                                            )}
                                        />
                                    }
                                    label="/ "
                                />
                                <FormHelperText id="component-helper-text">
                                     / 
                                </FormHelperText>
                            </FormControl>
                        </div>}

                        <Collapse
                            in={group.OptionsSerialized.archive_task === "true"}
                        >
                            <div className={classes.form}>
                                <FormControl fullWidth>
                                    <SizeInput
                                        value={
                                            group.OptionsSerialized
                                                .compress_size
                                        }
                                        onChange={handleOptionChange(
                                            "compress_size"
                                        )}
                                        min={0}
                                        max={9223372036854775807}
                                        label={""}
                                    />
                                </FormControl>
                                <FormHelperText id="component-helper-text">
                                    
                                    0 
                                </FormHelperText>
                            </div>

                            <div className={classes.form}>
                                <FormControl fullWidth>
                                    <SizeInput
                                        value={
                                            group.OptionsSerialized
                                                .decompress_size
                                        }
                                        onChange={handleOptionChange(
                                            "decompress_size"
                                        )}
                                        min={0}
                                        max={9223372036854775807}
                                        label={""}
                                    />
                                </FormControl>
                                <FormHelperText id="component-helper-text">
                                    
                                    0 
                                </FormHelperText>
                            </div>
                        </Collapse>
                    </div>
                </div>
                <div className={classes.root}>
                    <Button
                        disabled={loading}
                        type={"submit"}
                        variant={"contained"}
                        color={"primary"}
                    >
                        
                    </Button>
                </div>
            </form>
        </div>
    );
}
