    import React, { useCallback, useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import Input from "@material-ui/core/Input";
import FormHelperText from "@material-ui/core/FormHelperText";
import Button from "@material-ui/core/Button";
import API from "../../../middleware/Api";
import { useDispatch } from "react-redux";
import { toggleSnackbar } from "../../../actions";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import Switch from "@material-ui/core/Switch";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import AlertDialog from "../Dialogs/Alert";
import Alert from "@material-ui/lab/Alert";
import Link from "@material-ui/core/Link";

const useStyles = makeStyles(theme => ({
    root: {
        [theme.breakpoints.up("md")]: {
            marginLeft: 100
        },
        marginBottom: 40
    },
    form: {
        maxWidth: 400,
        marginTop: 20,
        marginBottom: 20
    },
    formContainer: {
        [theme.breakpoints.up("md")]: {
            padding: "0px 24px 0 24px"
        }
    }
}));

export default function Access() {
    const classes = useStyles();
    const [loading, setLoading] = useState(false);
    const [options, setOptions] = useState({
        register_enabled: "1",
        default_group: "1",
        email_active: "0",
        login_captcha: "0",
        reg_captcha: "0",
        forget_captcha: "0",
        qq_login: "0",
        qq_direct_login: "0",
        qq_login_id: "",
        qq_login_key: "",
        authn_enabled: "0",
        captcha_IsUseReCaptcha: "0",
        captcha_ReCaptchaKey: "defaultKey",
        captcha_ReCaptchaSecret: "defaultSecret"
    });
    const [siteURL, setSiteURL] = useState("");
    const [groups, setGroups] = useState([]);
    const [httpAlert, setHttpAlert] = useState(false);

    const handleChange = name => event => {
        let value = event.target.value;
        if (event.target.checked !== undefined) {
            value = event.target.checked ? "1" : "0";
        }
        setOptions({
            ...options,
            [name]: value
        });
    };

    const handleInputChange = name => event => {
        const value = event.target.value;
        setOptions({
            ...options,
            [name]: value
        });
    };

    const dispatch = useDispatch();
    const ToggleSnackbar = useCallback(
        (vertical, horizontal, msg, color) =>
            dispatch(toggleSnackbar(vertical, horizontal, msg, color)),
        [dispatch]
    );

    useEffect(() => {
        API.post("/admin/setting", {
            keys: [...Object.keys(options), "siteURL"]
        })
            .then(response => {
                setSiteURL(response.data.siteURL);
                delete response.data.siteURL;
                setOptions(response.data);
            })
            .catch(error => {
                ToggleSnackbar("top", "right", error.message, "error");
            });

        API.get("/admin/groups")
            .then(response => {
                setGroups(response.data);
            })
            .catch(error => {
                ToggleSnackbar("top", "right", error.message, "error");
            });
        // eslint-disable-next-line
    }, []);

    const submit = e => {
        e.preventDefault();
        setLoading(true);
        const option = [];
        Object.keys(options).forEach(k => {
            option.push({
                key: k,
                value: options[k]
            });
        });
        API.patch("/admin/setting", {
            options: option
        })
            .then(() => {
                ToggleSnackbar("top", "right", "", "success");
            })
            .catch(error => {
                ToggleSnackbar("top", "right", error.message, "error");
            })
            .then(() => {
                setLoading(false);
            });
    };

    return (
        <div>
            <AlertDialog
                title={""}
                msg={
                    "Web Authn  HTTPS  -  - URL  HTTPS "
                }
                onClose={() => setHttpAlert(false)}
                open={httpAlert}
            />
            <form onSubmit={submit}>
                <div className={classes.root}>
                    <Typography variant="h6" gutterBottom>
                        
                    </Typography>
                    <div className={classes.formContainer}>
                        <div className={classes.form}>
                            <FormControl fullWidth>
                                <FormControlLabel
                                    control={
                                        <Switch
                                            checked={
                                                options.register_enabled === "1"
                                            }
                                            onChange={handleChange(
                                                "register_enabled"
                                            )}
                                        />
                                    }
                                    label=""
                                />
                                <FormHelperText id="component-helper-text">
                                    
                                </FormHelperText>
                            </FormControl>
                        </div>

                        <div className={classes.form}>
                            <FormControl fullWidth>
                                <FormControlLabel
                                    control={
                                        <Switch
                                            checked={
                                                options.email_active === "1"
                                            }
                                            onChange={handleChange(
                                                "email_active"
                                            )}
                                        />
                                    }
                                    label=""
                                />
                                <FormHelperText id="component-helper-text">
                                    
                                </FormHelperText>
                            </FormControl>
                        </div>

                        <div className={classes.form}>
                            <FormControl fullWidth>
                                <FormControlLabel
                                    control={
                                        <Switch
                                            checked={
                                                options.reg_captcha === "1"
                                            }
                                            onChange={handleChange(
                                                "reg_captcha"
                                            )}
                                        />
                                    }
                                    label=""
                                />
                                <FormHelperText id="component-helper-text">
                                    
                                </FormHelperText>
                            </FormControl>
                        </div>

                        <div className={classes.form}>
                            <FormControl fullWidth>
                                <FormControlLabel
                                    control={
                                        <Switch
                                            checked={
                                                options.login_captcha === "1"
                                            }
                                            onChange={handleChange(
                                                "login_captcha"
                                            )}
                                        />
                                    }
                                    label=""
                                />
                                <FormHelperText id="component-helper-text">
                                    
                                </FormHelperText>
                            </FormControl>
                        </div>

                        <div className={classes.form}>
                            <FormControl fullWidth>
                                <FormControlLabel
                                    control={
                                        <Switch
                                            checked={
                                                options.forget_captcha === "1"
                                            }
                                            onChange={handleChange(
                                                "forget_captcha"
                                            )}
                                        />
                                    }
                                    label=""
                                />
                                <FormHelperText id="component-helper-text">
                                    
                                </FormHelperText>
                            </FormControl>
                        </div>

                        <div className={classes.form}>
                            <FormControl fullWidth>
                                <FormControlLabel
                                    control={
                                        <Switch
                                            checked={
                                                options.captcha_IsUseReCaptcha ===
                                                "1"
                                            }
                                            onChange={handleChange(
                                                "captcha_IsUseReCaptcha"
                                            )}
                                        />
                                    }
                                    label=" reCaptcha V2 "
                                />
                                <FormHelperText id="component-helper-text">
                                     reCaptcha V2 
                                </FormHelperText>
                            </FormControl>
                        </div>

                        {options.captcha_IsUseReCaptcha === "1" && (
                            <>
                                <div className={classes.form}>
                                    <FormControl fullWidth>
                                        <InputLabel htmlFor="component-helper">
                                            Site KEY
                                        </InputLabel>
                                        <Input
                                            required
                                            value={options.captcha_ReCaptchaKey}
                                            onChange={handleInputChange(
                                                "captcha_ReCaptchaKey"
                                            )}
                                        />
                                        <FormHelperText id="component-helper-text">
                                            <Link
                                                href={
                                                    "https://www.google.com/recaptcha/admin/create"
                                                }
                                                target={"_blank"}
                                            >
                                                
                                            </Link>{" "}
                                             
                                        </FormHelperText>
                                    </FormControl>
                                </div>

                                <div className={classes.form}>
                                    <FormControl fullWidth>
                                        <InputLabel htmlFor="component-helper">
                                            Secret
                                        </InputLabel>
                                        <Input
                                            required
                                            value={
                                                options.captcha_ReCaptchaSecret
                                            }
                                            onChange={handleInputChange(
                                                "captcha_ReCaptchaSecret"
                                            )}
                                        />
                                        <FormHelperText id="component-helper-text">
                                            <Link
                                                href={
                                                    "https://www.google.com/recaptcha/admin/create"
                                                }
                                                target={"_blank"}
                                            >
                                                
                                            </Link>{" "} 
                                        </FormHelperText>
                                    </FormControl>
                                </div>
                            </>
                        )}

                        <div className={classes.form}>
                            <FormControl fullWidth>
                                <FormControlLabel
                                    control={
                                        <Switch
                                            checked={
                                                options.authn_enabled === "1"
                                            }
                                            onChange={e => {
                                                if (
                                                    !siteURL.startsWith(
                                                        "https://"
                                                    )
                                                ) {
                                                    setHttpAlert(true);
                                                    return;
                                                }
                                                handleChange("authn_enabled")(
                                                    e
                                                );
                                            }}
                                        />
                                    }
                                    label="Web Authn"
                                />
                                <FormHelperText id="component-helper-text">
                                    
                                    HTTPS 
                                </FormHelperText>
                            </FormControl>
                        </div>

                        <div className={classes.form}>
                            <FormControl>
                                <InputLabel htmlFor="component-helper">
                                    
                                </InputLabel>
                                <Select
                                    value={options.default_group}
                                    onChange={handleInputChange(
                                        "default_group"
                                    )}
                                    required
                                >
                                    {groups.map(v => {
                                        if (v.ID === 3) {
                                            return null;
                                        }
                                        return (
                                            <MenuItem
                                                key={v.ID}
                                                value={v.ID.toString()}
                                            >
                                                {v.Name}
                                            </MenuItem>
                                        );
                                    })}
                                </Select>
                                <FormHelperText id="component-helper-text">
                                    
                                </FormHelperText>
                            </FormControl>
                        </div>
                    </div>
                </div>

                <div className={classes.root}>
                    <Typography variant="h6" gutterBottom>
                        QQ
                    </Typography>
                    <div className={classes.formContainer}>
                        <div className={classes.form}>
                            <Alert severity="info">{siteURL.endsWith("/") ? (siteURL + "#/login/qq"):(siteURL + "/#/login/qq")}</Alert>
                        </div>
                        <div className={classes.form}>
                            <FormControl fullWidth>
                                <FormControlLabel
                                    control={
                                        <Switch
                                            checked={options.qq_login === "1"}
                                            onChange={handleChange("qq_login")}
                                        />
                                    }
                                    label="QQ"
                                />
                                <FormHelperText id="component-helper-text">
                                    QQQQ
                                </FormHelperText>
                            </FormControl>
                        </div>

                        {options.qq_login === "1" && (
                            <>
                                <div className={classes.form}>
                                    <FormControl fullWidth>
                                        <FormControlLabel
                                            control={
                                                <Switch
                                                    checked={
                                                        options.qq_direct_login ===
                                                        "1"
                                                    }
                                                    onChange={handleChange(
                                                        "qq_direct_login"
                                                    )}
                                                />
                                            }
                                            label=""
                                        />
                                        <FormHelperText id="component-helper-text">
                                            QQQQ
                                        </FormHelperText>
                                    </FormControl>
                                </div>

                                <div className={classes.form}>
                                    <FormControl fullWidth>
                                        <InputLabel htmlFor="component-helper">
                                            APP ID
                                        </InputLabel>
                                        <Input
                                            required
                                            value={options.qq_login_id}
                                            onChange={handleInputChange(
                                                "qq_login_id"
                                            )}
                                        />
                                        <FormHelperText id="component-helper-text">
                                             APP ID
                                        </FormHelperText>
                                    </FormControl>
                                </div>

                                <div className={classes.form}>
                                    <FormControl fullWidth>
                                        <InputLabel htmlFor="component-helper">
                                            APP KEY
                                        </InputLabel>
                                        <Input
                                            required
                                            value={options.qq_login_key}
                                            onChange={handleInputChange(
                                                "qq_login_key"
                                            )}
                                        />
                                        <FormHelperText id="component-helper-text">
                                             APP KEY
                                        </FormHelperText>
                                    </FormControl>
                                </div>
                            </>
                        )}
                    </div>
                </div>

                <div className={classes.root}>
                    <Button
                        disabled={loading}
                        type={"submit"}
                        variant={"contained"}
                        color={"primary"}
                    >
                        
                    </Button>
                </div>
            </form>
        </div>
    );
}
